'use client';

import { Checkbox } from "@/components/ui/checkbox";
import { Label } from "@/components/ui/label";
import type { Skill } from "@/lib/types";
import { useSkills } from "@/hooks/use-skills";
import { Button } from "./ui/button";
import { BookOpen } from "lucide-react";

interface SkillAssessmentItemProps {
  skill: Skill;
  onGenerateGuide: (skillName: string) => void;
}

export function SkillAssessmentItem({ skill, onGenerateGuide }: SkillAssessmentItemProps) {
  const { state, toggleSkill } = useSkills();
  const isChecked = state.selectedSkills.has(skill.id);
  const uniqueId = `skill-${skill.id}`;
  
  return (
    <div className="flex items-center justify-between p-3 rounded-lg hover:bg-secondary/50 transition-colors">
      <div className="flex items-center space-x-3">
        <Checkbox
          id={uniqueId}
          checked={isChecked}
          onCheckedChange={() => toggleSkill(skill.id)}
          aria-labelledby={`${uniqueId}-label`}
        />
        <Label id={`${uniqueId}-label`} htmlFor={uniqueId} className="text-sm font-medium cursor-pointer">
          {skill.name}
        </Label>
      </div>
      <Button variant="ghost" size="sm" onClick={() => onGenerateGuide(skill.name)}>
        <BookOpen className="mr-2 h-4 w-4" />
        Guide
      </Button>
    </div>
  );
}
