'use client';

import { useState, useTransition, useEffect } from 'react';
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogDescription,
} from '@/components/ui/dialog';
import { useToast } from '@/hooks/use-toast';
import { getLearningGuideAction } from '@/app/actions';
import { Skeleton } from './ui/skeleton';
import { ScrollArea } from './ui/scroll-area';

interface GuideGeneratorProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  skillName: string | null;
}

export function GuideGenerator({ open, onOpenChange, skillName }: GuideGeneratorProps) {
  const { toast } = useToast();
  const [isPending, startTransition] = useTransition();
  const [guide, setGuide] = useState<string | null>(null);

  useEffect(() => {
    if (open && skillName && !guide && !isPending) {
      startTransition(async () => {
        const result = await getLearningGuideAction(skillName);
        if (result.success) {
          setGuide(result.data!);
        } else {
          toast({ variant: 'destructive', title: 'Error', description: result.error });
          onOpenChange(false);
        }
      });
    }
    if (!open) {
        setGuide(null);
    }
  }, [open, skillName, toast, onOpenChange, guide, isPending]);

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="sm:max-w-2xl">
        <DialogHeader>
          <DialogTitle>AI Learning Guide: {skillName}</DialogTitle>
          <DialogDescription>
            A step-by-step guide to help you master {skillName}. Generated by AI.
          </DialogDescription>
        </DialogHeader>
        <div className="py-4">
          {isPending && (
            <div className="space-y-4">
                <Skeleton className="h-6 w-1/2" />
                <Skeleton className="h-4 w-full" />
                <Skeleton className="h-4 w-full" />
                <Skeleton className="h-4 w-4/5" />
                 <Skeleton className="h-6 w-1/2 mt-6" />
                <Skeleton className="h-4 w-full" />
                <Skeleton className="h-4 w-full" />
                <Skeleton className="h-4 w-3/4" />
            </div>
          )}
          {guide && (
            <ScrollArea className="h-[60vh] pr-4">
              <div className="prose prose-sm dark:prose-invert max-w-none whitespace-pre-wrap font-sans" dangerouslySetInnerHTML={{ __html: guide }} />
            </ScrollArea>
          )}
        </div>
      </DialogContent>
    </Dialog>
  );
}
