'use client';

import { useState, useTransition } from 'react';
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogDescription,
  DialogFooter,
} from '@/components/ui/dialog';
import { Button } from './ui/button';
import { useSkills } from '@/hooks/use-skills';
import { useToast } from '@/hooks/use-toast';
import { getCareerRecommendationsAction } from '@/app/actions';
import { ALL_SKILLS } from '@/lib/skills';
import { Skeleton } from './ui/skeleton';
import { ScrollArea } from './ui/scroll-area';

interface CareerRecommenderProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
}

export function CareerRecommender({ open, onOpenChange }: CareerRecommenderProps) {
  const { state } = useSkills();
  const { toast } = useToast();
  const [isPending, startTransition] = useTransition();
  const [recommendations, setRecommendations] = useState<string | null>(null);

  const handleGetRecommendations = () => {
    const selectedSkillNames = Array.from(state.selectedSkills).map(id => {
      return ALL_SKILLS.find(skill => skill.id === id)?.name || '';
    }).filter(Boolean);

    if (selectedSkillNames.length === 0) {
      toast({
        variant: 'destructive',
        title: 'No Skills Selected',
        description: 'Please select some skills first to get career recommendations.',
      });
      return;
    }
    
    setRecommendations(null);
    startTransition(async () => {
      const result = await getCareerRecommendationsAction(selectedSkillNames.join(', '));
      if (result.success) {
        setRecommendations(result.data!);
      } else {
        toast({ variant: 'destructive', title: 'Error', description: result.error });
      }
    });
  };

  const handleOpenChange = (isOpen: boolean) => {
    if (!isOpen) {
        setRecommendations(null);
    }
    onOpenChange(isOpen);
  };


  return (
    <Dialog open={open} onOpenChange={handleOpenChange}>
      <DialogContent className="sm:max-w-2xl">
        <DialogHeader>
          <DialogTitle>AI Career Recommendations</DialogTitle>
          <DialogDescription>
            Get personalized web3 career path recommendations based on your selected skills.
          </DialogDescription>
        </DialogHeader>
        <div className="py-4">
          {!recommendations && !isPending && (
             <div className="text-center p-8 border-dashed border-2 rounded-lg">
                <p className="text-muted-foreground">Click the button below to generate your career paths.</p>
             </div>
          )}
          {isPending && (
            <div className="space-y-4">
                <Skeleton className="h-6 w-1/2" />
                <Skeleton className="h-4 w-full" />
                <Skeleton className="h-4 w-full" />
                <Skeleton className="h-4 w-4/5" />
                <Skeleton className="h-6 w-1/2 mt-6" />
                <Skeleton className="h-4 w-full" />
                <Skeleton className="h-4 w-full" />
                <Skeleton className="h-4 w-3/4" />
            </div>
          )}
          {recommendations && (
            <ScrollArea className="h-[50vh] pr-4">
                <div className="prose prose-sm dark:prose-invert max-w-none whitespace-pre-wrap font-sans" dangerouslySetInnerHTML={{ __html: recommendations }}/>
            </ScrollArea>
          )}
        </div>
        <DialogFooter>
          <Button onClick={handleGetRecommendations} disabled={isPending}>
            {isPending ? 'Generating...' : 'Generate Recommendations'}
          </Button>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
}
