'use client';

import { useState } from 'react';
import {
  Accordion,
  AccordionContent,
  AccordionItem,
  AccordionTrigger,
} from '@/components/ui/accordion';
import { SKILL_CATEGORIES } from '@/lib/skills';
import { useSkills } from '@/hooks/use-skills';
import { Skeleton } from './ui/skeleton';
import { SkillAssessmentItem } from './skill-assessment-item';
import { GuideGenerator } from './guide-generator';
import { Progress } from './ui/progress';

export function SkillAssessment() {
  const { state } = useSkills();
  const [guideSkill, setGuideSkill] = useState<string | null>(null);
  const [isGuideOpen, setIsGuideOpen] = useState(false);
  
  const totalSkills = SKILL_CATEGORIES.reduce((acc, cat) => acc + cat.skills.length, 0);
  const selectedCount = state.selectedSkills.size;
  const progressPercentage = totalSkills > 0 ? (selectedCount / totalSkills) * 100 : 0;

  const handleGenerateGuide = (skillName: string) => {
    setGuideSkill(skillName);
    setIsGuideOpen(true);
  };
  
  if (!state.isInitialized) {
    return (
        <div className="space-y-4">
            <Skeleton className="h-12 w-full" />
            <Skeleton className="h-12 w-full" />
            <Skeleton className="h-12 w-full" />
        </div>
    );
  }

  return (
    <div className="w-full max-w-4xl mx-auto">
      <div className='mb-8'>
        <div className="flex justify-between items-center mb-2">
            <h2 className="text-lg font-semibold">Your Progress</h2>
            <span className="text-lg font-semibold text-primary">{selectedCount} / {totalSkills} skills</span>
        </div>
        <Progress value={progressPercentage} className="w-full h-3" />
      </div>

      <Accordion type="multiple" className="w-full">
        {SKILL_CATEGORIES.map((category) => {
            const Icon = category.icon;
            return (
                <AccordionItem value={category.id} key={category.id}>
                <AccordionTrigger className="text-base font-semibold hover:no-underline">
                    <div className='flex items-center gap-3'>
                        <Icon className="h-5 w-5 text-accent" />
                        {category.name}
                    </div>
                </AccordionTrigger>
                <AccordionContent>
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-x-4 gap-y-1">
                        {category.skills.map((skill) => (
                           <SkillAssessmentItem key={skill.id} skill={skill} onGenerateGuide={handleGenerateGuide} />
                        ))}
                    </div>
                </AccordionContent>
                </AccordionItem>
            );
        })}
      </Accordion>
      <GuideGenerator 
        open={isGuideOpen}
        onOpenChange={setIsGuideOpen}
        skillName={guideSkill}
      />
    </div>
  );
}
