'use server';

import { generateLearningGuide } from '@/ai/flows/generate-learning-guide';
import { recommendCareerPaths } from '@/ai/flows/recommend-career-paths';

export async function getLearningGuideAction(skillOrRole: string) {
  try {
    const result = await generateLearningGuide({ skillOrRole });
    return { success: true, data: result.learningGuide };
  } catch (error) {
    console.error('Error generating learning guide:', error);
    return { success: false, error: 'Failed to generate learning guide.' };
  }
}

export async function getCareerRecommendationsAction(skillsetAssessment: string) {
  if (!skillsetAssessment.trim()) {
    return { success: false, error: 'Please select at least one skill to get recommendations.' };
  }
  try {
    const result = await recommendCareerPaths({ skillsetAssessment });
    return { success: true, data: result.careerPaths };
  } catch (error) {
    console.error('Error recommending career paths:', error);
    return { success: false, error: 'Failed to generate career recommendations.' };
  }
}
